* {
    box-sizing: border-box;
}

.app {
    margin: 2rem;
}

a {
    color: #08c;
}

code {
    background: #eee;
    padding: .1rem;
    font-family: 'Menlo';
    font-size: 13px;
    color: #ff00aa;
}

.login-form {
    padding: 1px 46px 3px 46px;
    background: #5b826008;
    border: 1px solid #8080803b;
    border-radius: 12px;
    margin-top: 55px;
}

.resetpwd-form {
    padding: 2px 28px;
    background: #5b826008;
    border: 1px solid #8080803b;
    border-radius: 12px;
    margin-top: 55px;
}

.col-form-label {
    font-size: 16px;
    font-weight: 500;
}

input {
    padding: .5rem;
    font-size: 16px;
    width: 100%;
    display: block;
    border-radius: 4px;
    border: 1px solid #ccc;
}

input:focus {
    border-color: #007eff;
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 0 3px rgba(0, 126, 255, 0.1);
    outline: none;
}

input.error {
    border-color: red;
}

label {
    font-weight: bold;
    display: block;
    margin-bottom: .5rem;
}

.input-feedback {
    color: red;
    margin-top: .25rem;
}

button {
    max-width: 150px;
    margin: 20px 0;
    padding: 12px 20px;
    border-style: none;
    border-radius: 5px;
    background-color: #08c;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
    font-size: 17px;
    font-weight: 500;
    color: #fff;
    cursor: pointer;
    outline: none;
    -webkit-appearance: none;
}

button:disabled {
    opacity: .5;
    cursor: not-allowed !important;
}

button+button {
    margin-left: .5rem;
}

button.outline {
    background-color: #eee;
    border: 1px solid #aaa;
    color: #555;
}

.intro {
    background-color: yellow;
}

.container {
    margin: 20px auto;
}

.form {
    padding: 200px;
}

.field {
    display: block;
    width: 100%;
}

.text_field {
    display: block;
    width: 100%;
    margin-top: 20px;
}

.field_text {
    margin-top: 5px;
    margin-bottom: 5px;
    padding: 7px;
    display: block;
    width: 100%;
    font-size: 12px;
    color: red;
    background-color: #ff9999;
}

.button {
    background-color: #6666ff;
    border-radius: 10px;
    color: white;
    padding: 8px;
    border: solid 2px #6666ff;
    margin: 10px;
}

.btn-primary {
    padding: 4px 26px;
    background-color: #1aabff;
    border-color: #1aabff;
}
.btn-primary:hover {
    padding: 4px 26px;
    background-color: #1aabff !important;
    border-color: #1aabff !important;
}

.forgot-pwd {
    margin-left: 17%;
}

.submit_button {
    background-color: #00ff00;
    border: solid 2px #00ff00;
}

.output {
    background-color: grey;
    color: white;
    display: block;
    margin: 20px 250px;
    white-space: pre;
    padding: 20px;
}

.overlay {
    border: 16px solid #f3f3f3;
    /* Light grey */
    border-top: 16px solid #3498db;
    /* Blue */
    border-radius: 50%;
    width: 120px;
    height: 120px;
    animation: spin 2s linear infinite;
    margin: 0 auto;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

.nav img {
    width: 180px;
    height: 51px;
}